<template>
    <div class="foodWrap">
        <header>
            <router-link :to="{path:'/search',query:{geoHash:this.geoHash}}">
                <i class="el-icon-search"></i>
            </router-link>
            <div class="ellipsis">{{geoInfo ? geoInfo.name : ""}}</div>
            <router-link to="/login">
                <span>|</span>
            </router-link>
        </header>
        <div class="lunbo">
            <swiper  :options="swiperOption">
                <swiper-slide >
                    <div v-for="(img,index) in imgArr" v-if="index < 8" class="img1" @click="toDetails(img.title)">
                            <div class="img2">
                                <img :src="'https://fuss10.elemecdn.com'+img.image_url" class="img3" >
                                <span class="sp1">{{img.title}}</span>
                            </div>

                    </div>
                </swiper-slide>
                <swiper-slide>
                    <div v-for="(img,index) in imgArr" v-if="index >=8 && index <16" class="img1" @click="toDetails(img.title)">
                        <div class="img2">
                            <img :src="'https://fuss10.elemecdn.com'+img.image_url" class="img3">
                            <span class="sp1">{{img.title}}</span>
                        </div>
                    </div>
                </swiper-slide>
                <div class="swiper-pagination" slot="pagination"></div>
            </swiper>
        </div>
        <div class="shop_list">
            <div class="shop_header">
                <i class="el-icon-takeaway-box ii1"></i>
                <span class="shop_header_title"></span>
            </div>
        </div>
        <div class="shoplist_container">
            <ul>
                <li v-for="p in shopArr" :key="p.id" class="shop_li" @click="GoToStore(p)">
                    <div><img :src="'https://elm.cangdu.org/img/'+p.image_path" alt=""></div>
                    <div class="shop_right">
                    <div class="shop_detail_header">
                        <div class="div1"></div>
                        <h4 class="shop_title">{{p.name}}</h4>
                        <ul class="shop_detail_ul">
                            <li class="supports"></li>
                            <li class="supports"></li>
                            <li class="supports"></li>
                        </ul>
                    </div>
                    <h5 class="rating_order_num">
                        <section class="rating_order_num_left">
                            <section class="rating_section">
                                <div class="rating_container">
                                    <section class="star_container">
                                        <!--<el-rate-->
                                                <!--v-model="p.rating"-->
                                                <!--disabled-->
                                                <!--text-color="#ff9900"-->
                                                <!--class="rating"-->
                                        <!--&gt;-->
                                        <!--</el-rate>-->
                                    </section>
                                </div>
                                <span class="rating_num">{{p.rating}}</span>
                            </section>
                            <section class="order_section">{{p.recent_order_num}}</section>
                        </section>
                        <section class="rating_order_num_right">
                            <span class="delivery_left"></span>
                            <span class="delivery_right"></span>
                        </section>
                    </h5>
                        <h5 class="fee_distance">
                            <p class="fee">{{p.float_minimum_order_amount}}<span>
                                /</span>{{p.float_delivery_fee}}</p>
                            <p class="distance_time">
                                <span>{{p.distance}}</span>
                                <span>/</span>
                                <span class="order_time">{{p.order_lead_time}}</span>
                            </p>
                        </h5>
                    <div></div>
                    </div>
                </li>
            </ul>
        </div>
        <section class="foot_guide">
            <section class="guide_item" @click="toFood">
                <i class="el-icon-eleme" style="color: #3190e8"></i>
                <span></span>
            </section>
            <section class="guide_item" @click="toSearch">
                <i class="el-icon-zoom-out"></i>
                <span></span>
            </section>
            <section class="guide_item" @click="toOrder">
                <i class="el-icon-shopping-cart-2"></i>
                <span></span>
            </section>
            <section class="guide_item" @click="toUser">
                <i class="el-icon-user"></i>
                <span></span>
            </section>
        </section>



    </div>
</template>

<script>
    import { Swiper, SwiperSlide, } from 'vue-awesome-swiper';
    import "../../node_modules/swiper/css/swiper.css";
    export default {
        components: {
            Swiper,
            SwiperSlide
        },
        name: "Food",
        data(){
            return{
                geohash:0,
                geoInfo:"",
                imgArr: [],
                shopArr:[],
                masite:[],
                latitude:"",
                longitude:'',
                swiperOption: {
                    loop: true,
                    autoplay: 2000,
                },
            }
        },
        created(){
            this.msite = JSON.parse(localStorage.getItem("msite"));
            console.log(this.msite);
            // this.geoHash = this.$route.query.cGeohash;
            // console.log(this.geoHash);
            this.axios.get("/v2/pois/"+this.msite.geohash).then(res=>{
                this.geoInfo = res;
                console.log(this.geoInfo);
                this.latitude = this.geoInfo.latitude;
                this.longitude = this.geoInfo.longitude;
            }).catch(err=>{});
            this.axios.get("v2/index_entry").then(res=>{
                this.imgArr = res;
                console.log(this.imgArr);
            }).catch(err=>{});
            this.axios.get("shopping/restaurants?",{
                params:{
                    latitude:this.msite.latitude,
                    longitude:this.msite.longitude
                }
            }).then(res=>{
                    this.shopArr = res;
                    console.log(this.shopArr);
                }).catch(err=>{});
        },
        methods:{
            toDetails(name){
                this.$router.push({path:'/details',query:{
                    geoHash:this.geoHash,
                    geoInfo:this.geoInfo,
                    shopArr:this.shopArr,
                    name :name
                    }}).catch(err=>{});
            },
            GoToStore(p){
                this.$router.push('/storedetails');
                localStorage.setItem("shop",JSON.stringify(p));
                // localStorage.setItem("storedetails",JSON.stringify(p));
            },
            toUser(){
                this.$router.push('/userinfo');
            },
            toFood(){
                this.$router.push('/food');
            },
            toSearch(){
                this.$router.push('/search');
            },
            toOrder(){
                this.$router.push('/order');
            },
        }
    }
</script>

